body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.raffle-form {
}

.raffle-form .-actions {
  margin: 1rem 0;
}

.raffle-form .-actions button {
  margin-right: 15px;
}

@media print {
  .raffle-form {
    display: none;
  }
}

.raffle-ticket {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px;
}

.raffle-ticket .separator {
  height: 100%;
  border-left: 1px dashed #ccc;
}

.raffle-ticket .form-control.-user-fill {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-radius: 0;
}

.raffle-ticket .content {
  text-align: center;
}

.raffle-ticket .content > h1 {
  text-transform: uppercase;
  font-size: 12px;
}

.raffle-ticket .content > h2 {
  font-size: 24px;
}

.raffle-ticket .price {
  font-weight: bold;
  font-size: 1.2rem;
}

.raffle-ticket .number {
  font-face: monospace;
  font-weight: bold;
  font-size: 1.2rem;
}

.raffle-ticket .description {
  font-size: 14px;
}

@media print {
  .raffle-ticket {
    page-break-inside: avoid;
  }
}

.form-region {
  background: #f6f6f6;
  border-bottom: 1px solid #ddd;
  padding: 30px 0;
}

@media print {
  .form-region {
    display: none;
  }
}

